"use client";

import { useState } from "react";
import { useI18n } from "@/i18n/i18nProvider";

const BACKEND_URL = process.env.NEXT_PUBLIC_BACKEND_URL!;

type PreviewRow = {
  id: string;
  trackingNumber: string;
  companyName: string;
  provider: string;
  originCountry: string;
  destinationCountry: string;
  invoicedPrice: number;
  invoicedWeight: number | null;
};

type RawInvoice = {
  id?: string;
  shipment?: {
    trackingNumber?: string;
    provider?: string;
    originCountry?: string;
    destinationCountry?: string;
    company?: { name?: string };
  };
  invoicedPrice?: number | string;
  invoicedWeight?: number | string;
};

type UploadInvoicesDialogProps = {
  onUploaded?: () => void;
};

export function UploadInvoicesDialog({
  onUploaded,
}: UploadInvoicesDialogProps) {
  const { t } = useI18n();

  const [open, setOpen] = useState(false);
  const [file, setFile] = useState<File | null>(null);
  const [previewRows, setPreviewRows] = useState<PreviewRow[]>([]);
  const [error, setError] = useState<string | null>(null);
  const [isUploading, setIsUploading] = useState(false);
  const [isParsing, setIsParsing] = useState(false);

  const resetState = () => {
    setFile(null);
    setPreviewRows([]);
    setError(null);
    setIsUploading(false);
    setIsParsing(false);
  };

  const handleOpen = () => {
    setOpen(true);
    resetState();
  };

  const handleClose = () => {
    setOpen(false);
    resetState();
  };

  const handleFileChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const selected = e.target.files?.[0] ?? null;
    setFile(selected);
    setPreviewRows([]);
    setError(null);

    if (!selected) return;

    try {
      setIsParsing(true);
      const text = await selected.text();
      const parsed = JSON.parse(text) as unknown;

      const arr: unknown[] = Array.isArray(parsed) ? parsed : [parsed];

      const rows: PreviewRow[] = arr.map((item, index) => {
        const raw = item as RawInvoice;

        const shipment = raw.shipment ?? {};
        const company = shipment.company ?? {};

        return {
          id: raw.id ?? `row-${index}`,
          trackingNumber: shipment.trackingNumber ?? "N/A",
          companyName: company.name ?? "Unknown",
          provider: shipment.provider ?? "N/A",
          originCountry: shipment.originCountry ?? "-",
          destinationCountry: shipment.destinationCountry ?? "-",
          invoicedPrice: Number(raw.invoicedPrice ?? 0),
          invoicedWeight:
            raw.invoicedWeight !== undefined
              ? Number(raw.invoicedWeight)
              : null,
        };
      });

      setPreviewRows(rows);
    } catch (err) {
      console.error(err);
      setError(t("uploadDialog.invalidJson"));
    } finally {
      setIsParsing(false);
    }
  };

  const handleConfirmUpload = async () => {
    if (!file) {
      setError(t("uploadDialog.invalidJson"));
      return;
    }

    const formData = new FormData();
    formData.append("files", file);

    setIsUploading(true);
    setError(null);

    try {
      const res = await fetch(`${BACKEND_URL}/upload-invoices`, {
        method: "POST",
        body: formData,
      });

      if (!res.ok) {
        const text = await res.text();
        throw new Error(`Upload failed: ${res.status} ${text}`);
      }

      await res.json();
      handleClose();
      if (onUploaded) onUploaded();
    } catch (err) {
      console.error(err);
      setError(t("uploadDialog.errorUpload"));
    } finally {
      setIsUploading(false);
    }
  };

  return (
    <>
      <button
        onClick={handleOpen}
        className="inline-flex items-center rounded-full bg-emerald-500 hover:bg-emerald-600 text-white text-sm font-semibold px-4 py-2 shadow-sm"
      >
        {t("dashboard.uploadInvoices")}
      </button>

      {open && (
        <div className="fixed inset-0 z-40 flex items-center justify-center bg-slate-900/50">
          <div className="bg-white rounded-2xl shadow-xl w-[min(900px,90vw)] max-h-[80vh] flex flex-col">
            {/* header */}
            <div className="px-6 py-4 border-b border-slate-100 flex items-center justify-between">
              <h2 className="text-lg font-semibold text-slate-800">
                {t("uploadDialog.title")}
              </h2>
              <button
                onClick={handleClose}
                className="text-slate-400 hover:text-slate-600 text-sm"
              >
                ✕
              </button>
            </div>

            {/* body */}
            <div className="flex-1 px-6 py-4">
              {isParsing && (
                <div className="text-sm text-slate-500">
                  {t("uploadDialog.parsing")}
                </div>
              )}

              {!isParsing && previewRows.length === 0 && !error && (
                <div className="h-full flex items-center justify-center text-sm text-slate-400">
                  {t("uploadDialog.helperText")}
                </div>
              )}

              {error && (
                <div className="mb-3 text-sm text-red-600 bg-red-50 border border-red-200 rounded-md px-3 py-2">
                  {error}
                </div>
              )}

              {previewRows.length > 0 && (
                <div className="border border-slate-200 rounded-xl overflow-hidden">
                  <div className="max-h-64 overflow-auto">
                    <table className="min-w-full text-xs">
                      <thead className="bg-slate-50 border-b border-slate-200">
                        <tr>
                          <th className="px-3 py-2 text-left font-semibold text-slate-500">
                            Tracking #
                          </th>
                          <th className="px-3 py-2 text-left font-semibold text-slate-500">
                            {t("dashboard.company")}
                          </th>
                          <th className="px-3 py-2 text-left font-semibold text-slate-500">
                            Provider
                          </th>
                          <th className="px-3 py-2 text-left font-semibold text-slate-500">
                            Route
                          </th>
                          <th className="px-3 py-2 text-right font-semibold text-slate-500">
                            Price
                          </th>
                          <th className="px-3 py-2 text-right font-semibold text-slate-500">
                            Weight
                          </th>
                        </tr>
                      </thead>
                      <tbody>
                        {previewRows.map((row) => (
                          <tr
                            key={row.id}
                            className="border-b border-slate-100 last:border-0"
                          >
                            <td className="px-3 py-2 text-slate-800">
                              {row.trackingNumber}
                            </td>
                            <td className="px-3 py-2 text-slate-600">
                              {row.companyName}
                            </td>
                            <td className="px-3 py-2 text-slate-600">
                              {row.provider}
                            </td>
                            <td className="px-3 py-2 text-slate-600">
                              {row.originCountry} → {row.destinationCountry}
                            </td>
                            <td className="px-3 py-2 text-right text-emerald-600 font-semibold">
                              {row.invoicedPrice.toFixed(2)}
                            </td>
                            <td className="px-3 py-2 text-right text-slate-600">
                              {row.invoicedWeight ?? "-"}
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </div>
                </div>
              )}
            </div>

            {/* footer */}
            <div className="px-6 py-3 border-t border-slate-100 flex items-center justify-between">
              <label className="inline-flex items-center text-xs text-slate-500 gap-2 cursor-pointer">
                <span className="inline-flex items-center rounded-full border border-slate-200 px-3 py-1 text-xs bg-slate-50">
                  {file?.name ?? t("uploadDialog.pickFilePlaceholder")}
                </span>
                <input
                  type="file"
                  accept="application/json"
                  className="hidden"
                  onChange={handleFileChange}
                />
                <span className="text-emerald-600 text-xs underline">
                  {t("uploadDialog.changeFile")}
                </span>
              </label>

              <div className="flex gap-2">
                <button
                  onClick={handleClose}
                  className="px-4 py-2 rounded-full border border-slate-200 text-sm text-slate-600 hover:bg-slate-50"
                >
                  {t("uploadDialog.cancel")}
                </button>
                <button
                  onClick={handleConfirmUpload}
                  disabled={isUploading || !file}
                  className="px-4 py-2 rounded-full bg-emerald-500 hover:bg-emerald-600 text-white text-sm font-semibold disabled:opacity

50 disabled:cursor-not-allowed"
                >
                  {isUploading
                    ? t("uploadDialog.uploading")
                    : t("uploadDialog.confirm")}
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </>
  );
}
